import kotlinx.datetime.Clock
import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.SerializationException
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json
import kotlinx.serialization.json.JsonElement
import kotlinx.serialization.json.JsonObject
import kotlinx.serialization.json.JsonPrimitive
import kotlinx.serialization.json.jsonObject
import kotlin.time.ExperimentalTime

const val INDEX = "index"

fun setupStore() {
    if (retrieve(INDEX).isEmpty()) {
        // Make sure there is an index to manage
        store(INDEX, "{}")
        // Create and store Oslo data as an example
        val d = """{"name":"Oslo","latitude":"N59.9394539","longitude":"E10.722508","azimuth":"221.56","altitude":"0","year":"2035","min_alt_above":"10","tz_offset_hour":"00:00","width":"297","height":"210","minutes_60":true,"hours_arabic":true,"tz_mult_15":true,"elong_30":true,"symbols_zodiac":true,"clip_horizon":false,"show_eot":false,"anniversary":"1814-05-17T11:00Z",align_horizon":true}"""
        if (store("Oslo",d))
            manage("Oslo")
    }
}
@OptIn(ExperimentalSerializationApi::class)
val jetson = Json {
    ignoreUnknownKeys = true
    prettyPrint = true
    prettyPrintIndent = "  "
}

fun loadJsonMap(name: String) =
    try {
        jetson.parseToJsonElement(
            retrieve(name).let {
                it.ifEmpty { "{}" }
            }
        ).jsonObject.toMutableMap()
    } catch (iaex: IllegalArgumentException) {
        println("storage#loadJsonMap() exception: ${iaex::class.simpleName} message: ${iaex.message}")
        emptyMap<String,JsonElement>().toMutableMap()
    }

fun saveJsonMap(name: String,map: Map<String, JsonElement>) =
    try {
        // Save the map with modifications
        val data = jetson.encodeToString(JsonObject(map.toMap()))

        store(name, data).also { if (it) manage(name) }
    } catch (sex: SerializationException) {
        println("storage#saveJsonMap() exception: ${sex::class.simpleName} message: ${sex.message}")
        false
    }

/**
 * Maintain an index for each experiment stored. Only experiments should be
 * managed by this function
 */
@OptIn(ExperimentalTime::class)
fun manage(key: String) {
    // Keep an index file for all experiments
    Json.decodeFromString<JsonObject>(retrieve(INDEX)).toMutableMap().also { index ->
        // Update the timestamp for experiment 'key'
        index[key] = JsonPrimitive(Clock.System.now().toEpochMilliseconds())
        while (index.size > 10) {
            // Delete experiments with the oldest timestamps until only 10 experiments remain
            index.entries.minBy { e ->
                e.value.toString().toLong()
            }.also { oldest ->
                if (delete(oldest.key))
                    if (index.remove(oldest.key) != null)
                        println("${oldest.key} has been deleted")
            }
        }
        // Write back the updated index
        store(INDEX,Json.encodeToString(index.toMap()))
    }
}

/**
 * Get the keys (names) of all experiments stored, sorted by time of access
 * @return list of names
 */
fun allExperiments() = Json.decodeFromString<JsonObject>(retrieve(INDEX)).
toMap().entries.sortedBy {
    -it.value.toString().toLong()
}.map {
    it.key
}

/**
 * Store a key - value pair
 * @return true if successfully stored
 */
expect fun store(key: String,data: String): Boolean

/**
 * Delete a key - value pair
 * @return true if successfully deleted
 */
expect fun delete(key: String): Boolean

/**
 * Retrieve a value by its key
 * @return a value string if it exists else an empty string
 */
expect fun retrieve(key: String): String

