import kotlinx.browser.localStorage
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.json.JsonObject
import org.w3c.dom.get
import kotlin.js.Json

actual fun store(key: String, data: String) = key.trim().let { k ->
        if (k.isNotEmpty())
            localStorage.setItem(k,data.trim())

        localStorage[k] != null
    }


actual fun retrieve(key: String) =
    localStorage[key.trim()] ?: ""


actual fun delete(key: String) = key.trim().let { k ->
        if (k.isNotEmpty())
            localStorage.removeItem(k)

        localStorage[k] == null
    }
